.menu__desktop {
  display: none;

  @media (min-width: 1024px) {
    float: right;
    display: block;
    text-align: center;
  }

  ul {
    display: inline-block;
    list-style: none;
    padding: 0;
    margin: 0;
    border: 0;
    line-height: 41px;

    li {
      display: inline-block;
      padding: 0;

      a {
        display: inline-block;
        color: $brand-primary;
        font-size: 14px;
        line-height: 41px;
        text-decoration: none;
        // padding: 0px 10px;
        padding: 0px 8px;
        text-rendering: optimizeLegibility;
        transition: all 300ms ease-in-out;
        position: relative;

        &.active {
          text-decoration: underline;
        }
        &:hover {
          text-decoration: underline;
        }

        // Linea con Pseudo elemento para indicar pagina activa
        // &.hover:after,
        // &.active:after {
        //   position: absolute;
        //   content: '';
        //   width: 0%;
        //   height: 1px;
        //   left: 0;
        //   bottom: 0;
        //   opacity: 0;
        //   z-index: 1;
        //   background-color: $brand-primary;
        //   transition: all 300ms ease-in-out;
        // }

        // &.hover:hover:after {
        //   opacity: 1;
        //   width: 100%;
        // }

        // &.active {
        //   font-weight: 700;
        // }

        // &.active:after {
        //   opacity: 1;
        //   width: 100%;
        // }
      }

      &:first-child {
        padding-left: 0;
      }

      &:nth-child(3) a {
        // margin-right: 10px;
        margin-right: 8px;
      }

      // Ingresa
      &:nth-child(4) a {
        // padding-left: 20px;
        padding-left: 16px;
        border-left: 1px solid #ebebeb;
      }

      // Ingresa + Registrate
      &:nth-child(4) a,
      &:nth-child(5) a {
        color: #888888;
      }

      // Icono Help
      &:last-child a {
        display: block;
        width: 15px;
        height: 41px;
        padding: 0;
        // margin-right: 10px;
        margin-right: 8px;
        text-indent: -31000px;

        background: url('#{$landing_url}/img/help.svg') no-repeat center center;
        background-size: 15px 15px;

        @media (min-width: 1280px) {
          margin-right: 0;
        }
      }
    }
  }
}
