.payment {
  text-align: center;
  border-top: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;
  // padding: $gutter 0;
  padding: 24px 0;

  &__container {
    @include outer-container(1200px);

    @media (min-width: 1024px) {
      @include display(flex);
      @include flex-direction(row);
      @include justify-content(center);
      @include align-items(center);
      @include flex-wrap(nowrap);
    }
  }

  @media (max-width: 1023px) {
    &__info,
    &__slider,
    &__btn {
      @include fill-parent;
      @include clearfix;
    }

    &__info,
    &__slider {
      margin-bottom: 16px;
    }
  }

  @media (min-width: 1024px) {
    &__info {
      float: left;
      width: 20%;
    }

    &__slider {
      float: left;
      width: 55%;
    }

    &__btn {
      float: left;
      width: 25%;
      // padding: 0;
    }
  }

  &__info {
    @media (max-width: 1279px) {
      // padding: 10px;
    }

    p {
      margin: 0;
      font-size: 1.1em;
      line-height:1;
      color: $brand-primary;
      text-transform: uppercase;

      strong {
        white-space: nowrap;
      }
    }
  }

  // Logos bancos - Insertados con JS
  &__logo {
    height: 66px;
    display: inline-block;
    vertical-align: middle;

    @include display(flex);
    @include flex-direction(row);
    @include justify-content(center);
    @include align-items(center);
    @include flex-wrap(nowrap);
  }

  // Info cuotas y fechas - Insertados con JS
  &__logo-info {
    p {
      font-size: 10px;
      line-height: 1.2;
      color: #666666;
      margin-bottom: 0;

      b {
        font-size: 12px;
      }
    }
  }

  // Btn Ver promociones
  &__btn {
    @media (max-width: 1023px) {
      // padding: 20px;
    }

    @media (min-width: 1024px) and (max-width: 1279px) {
      // padding: 15px;
    }

    a {
      display: inline-block;
      margin: 0;
      padding: 8px 16px;
      border-radius: 4px;

      color: #ffffff;
      background-color: $brand-primary;
      transition: all 300ms ease-in-out;

      &:hover {
        background-color: #00b2eb;
      }

      &:active,
      &:focus {
        background-color: #008ed0;
        -webkit-box-shadow: 0 5px 10px #115b9e inset;
        box-shadow: 0 5px 10px #115b9e inset;
      }
    }
  }
}
